<template>
    <div class="autherinfo">
      <div class="authersummay">
        <div class="topbar">‰ΩúËÄÖ</div>
        <router-link :to="{
              name:'user_info',
              params:{
                name: userinfo.loginname
              }
              }">
          <img :src="userinfo.avatar_url" alt="icon">
        </router-link>
        <span>{{userinfo.score}}ÁßØÂàÜ</span>
      </div>
      <div class="recent_replies">
        <div class="topbar">‰ΩúËÄÖÊúÄËøë‰∏ªÈ¢ò</div>
        <ul>
          <li v-for="list in topcilimitby5">
            <router-link :to="{
            name:'post_content',
            params: {
              id:list.id,
              name:list.author.loginname
            }
            }">
              {{list.title}}
            </router-link>
          </li>
        </ul>
      </div>
      <div class="recent_topics">
        <div class="topbar">‰ΩúËÄÖÊúÄËøëÂõûÂ§ç</div>
        <ul>
          <li v-for="list in replylimitby5">
            <router-link :to="{
            name:'post_content',
            params: {
              id:list.id,
              name:list.author.loginname
            }
            }">
            {{list.title}}
            </router-link>
          </li>
        </ul>
      </div>
    </div>
</template>

<script>
    export default { // ÂêëÂ§ñÈªòËÆ§ÈÖçÁΩÆ‰∏Ä‰∏™ÈÖçÁΩÆÂØπË±°
      name:"SlideBar",
      data () {
        return {
          isLoading:false,
          userinfo:{}
        }
      },
      computed:{
        topcilimitby5(){
          if(this.userinfo.recent_topics){
            return this.userinfo.recent_topics.slice(0,5);
          }
        },
        replylimitby5(){
          if(this.userinfo.recent_replies){
            return this.userinfo.recent_replies.slice(0,5);
          }
        }
      },
    methods: {
      getData() {
        this.$http.get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
          .then(res=>{
            this.isLoading = false; // Âä†ËΩΩÊàêÂäüÂéªÈô§Âä®Áîª
            this.userinfo = res.data.data;
            console.log(res.data.data)
          })
          .catch(function (err) {
            //Â§ÑÁêÜËøîÂõûÂ§±Ë¥•ÂêéÁöÑÈóÆÈ¢ò
            console.log(err)
          })
      }
    },
    beforeMount() {
      this.isLoading = true;
      this.getData();
    }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .authersummay, .recent_replies, .recent_topics {
    background-color: #fff;
  }
  .autherinfo {
    width: 328px;
    float: right;
    margin-top: 0;
  }
  li{
    padding: 3px 0 ;
  }
  .recent_replies ul, .recent_topics ul {
    margin-top: 0px;
    margin-bottom: 0px;
    list-style: none;
    padding-left: 14px;
  }

  ul a {
    font-size: 12px;
    text-decoration: none;
    color: #778087;
  }

  .topbar {
    padding: 10px;
    background-color: #f6f6f6;
    height: 16px;
    font-size: 12px;
    margin-top: 10px;
  }

  img {
    height: 48px;
    width: 48px;
    border-radius: 3px;
    margin: 10px;
  }

  .loginname {
    width: 100px;
    float: right;
    margin-top: 22px;
    margin-right: 159px;
    font-size: 14px;
  }

  .loginname a {
    text-decoration: none;
    color: #778087;
  }

  .authersummay .topbar {
    margin-top: 0px;
  }
</style>



